<template>
  <div>
    <aph></aph>
    <!---->
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <a href=""></a>&gt;<a href=""></a>&gt;<a href=""></a>
        </h2>
      </div>
    </div>
    <!---->
    <div class="main container">
      <template v-if="pdata !== null">
        <div class="pd_info clearfloat">
          <div class="pdinfo_img">
            <img :src="require('../assets/'+pdata.pic)" alt=""/>
          </div>
          <div class="pdinfo_text">
            <h2></h2>
            <ul>
              <li>{{pdata.model}}</li>
              <li>{{pdata.func}}</li>
              <li>{{pdata.noise}}</li>
              <li>{{pdata.wind}}</li>
              <li>{{pdata.applyTo}}</li>
              <li>{{pdata.size}}</li>
              <li>{{pdata.level}}</li>
            </ul>
            <p><span></span><strong id="price">{{pdata.price}}</strong></p>
            <a @click.prevent="gocart()" href="" id="addCart"><span class="icon_cart"></span></a>
          </div>
        </div>
        <div class="details_box">
          <h2 class="title"></h2>
          <div class="pd_details" v-html="pdata.detail"></div>
