</template>
<script>
import axios from 'axios';

export default {
  data:function(){
    return{
      pdata:null, //
    }
  },
  methods:{
    gaindata(){
      let url = `http://127.0.0.1/data/product_detail.php?pid=${this.$route.params.pid}`;
      axios.get(url).then((res)=>{
        this.pdata = res.data;
      }).catch((err)=>{
        console.log(err);
      });
    },
    gocart(){
      if(sessionStorage.uid){
        let url = `http://127.0.0.1/data/cart_detail_add.php?uid=1&pid=${this.$route.params.pid}`;
        axios.get(url).then((res)=>{
          console.log(res);
          if(res.data.code == 1){
            alert('');
          }else{
            alert('.!');
          }
        }).catch((err)=>{
          console.log(err);
        });
      }
    }
  },
  created:function(){
    this.gaindata();
  }
}
</script>

