$(function(){
    $(".header_box").load("header.html",function(){
        main();
        navText("");
    });
    $(".footer").load("footer.html");

    //nid
    var str=window.location.href;
    var pid=str.substr(str.lastIndexOf("=")+1);
    $.ajax({
        type:"post",
        url:"data/product_detail.php",
        data:{pid:pid},
        success:function(d){
            //console.log(d);
            $(".pdinfo_img>img").attr("src",d.pic);
            $(".pdinfo_text>h2").append(d.title1).attr("data-pid",d.pid);;
            var listHtml="";
            listHtml+='<li>'+ d.model+'</li>';
            listHtml+='<li>'+ d.func+'</li>';
            listHtml+='<li>'+ d.noise+'</li>';
            listHtml+='<li>'+ d.wind+'</li>';
            listHtml+='<li>'+ d.applyTo+'</li>';
            listHtml+='<li>'+ d.size+'</li>';
            listHtml+='<li>'+ d.level+'</li>';
            $(".pdinfo_text>ul").append(listHtml);
            $("#price").append(d.price);
            $(".pd_details").append(d.detail);
        }
    });

    $("#addCart").click(function(e){
        e.preventDefault();
        if(!sessionStorage.uid){
            location.href="login.html";
        }else{
            var uid=sessionStorage["uid"];
            var pid=$(".pdinfo_text>h2").attr("data-pid");
            $.ajax({
                type:"post",
                url:"data/cart_detail_add.php",
                data:{uid:uid,pid:pid},
                success:function(d){
                    //console.log(d);
                    if(d.code==1){
                        alert("");
                        cartUpdate(uid);
                    }
                }
            });
        }
    });

})