<template>
  <div>
    <aph></aph>
    <!---->
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <a href="#"></a>&gt;<a href="#"></a>
        </h2>
      </div>
    </div>
    <!---->
    <div class="main container">
      <div class="pl_header">
        <router-link to="/product/1" :class="{cur: type == 1 ? true : false}"></router-link>
        <router-link to="/product/2" :class="{cur: type == 2 ? true : false}"></router-link>
      </div>
      <!-- -->
      <ul v-if="pdata !== null" class="product_list clearfloat">
        <li v-for="(v,i) in pdata.data" :key="i">
          <router-link :to="'/pdetails/'+v.pid"><img :src="require(`../assets/${v.pic}`)"/></router-link>
          <div class="pdlist_text clearfloat">
            <h3>
              <p>{{v.title1}}</p>
              <span>{{v.title2}}</span>
            </h3>
            <router-link to=""></router-link>
          </div>
        </li>
      </ul>
      <!-- -->
      <div v-if="pdata !== null" class="pages">
        <a @click.prevent="loadData(type,parseInt(pdata.pageNum) - 1)" :class="{default: pdata.pageNum == 1 ? true :false}"></a>
        <a v-for="(v,i) in pdata.pageCount" @click.prevent="loadData(type,v)" :class="{cur: v == pdata.pageNum ? true : false}" :key="i">{{v}}</a>
        <a @click.prevent="loadData(type,parseInt(pdata.pageNum) + 1)" :class="{default: pdata.pageNum == pdata.pageCount ? true : false}"></a>
      </div>
    </div>
    <apf></apf>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data(){
    return{
      type:null, //
      pdata:null, //
    }
  },
  methods:{
    loadData:function(type,pnum=1){
      let url = `http://127.0.0.1/data/product_select.php?pageNum=${pnum}&type=${type}`
      axios.get(url).then((res=>{
        this.pdata = res.data;
        console.log(res.data);
      })).catch((error)=>{
        console.log(error);
      })
    }
  },
  created:function() {
    this.type = this.$route.params.type;
    this.loadData(this.type);
  },
  watch:{
    $route:function(){
      this.type = this.$route.params.type;
      this.loadData(this.type);
    }
  }
}
</script>
